import React from 'react'
import './skills.css';
import redux from '../../assets/redux.png'
import html from '../../assets/html.png';
import css from '../../assets/css.png';
import javascript from '../../assets/javascript.png';
import chakra from '../../assets/chakra.png';
import react from '../../assets/react.png';
import mongo from '../../assets/mongo.png';
import express from '../../assets/express.png';
import node from '../../assets/node.png';
import { useState,useEffect } from 'react';
const Skills = () => {
  const skills = [
    {
      name: 'HTML',
      imageSrc: html,
    },
    {
      name: 'CSS',
      imageSrc: css,
    },
    {
      name: 'JavaScript',
      imageSrc: javascript,
    },
    {
      name: 'Chakra UI',
      imageSrc: chakra,
    },
    {
      name: 'Redux',
      imageSrc:redux,
    },
    {
      name:"React.js",
      imageSrc:react
    },
    {name:"MongoDB",imageSrc:mongo},
    {name:"Express.js",imageSrc:express},
{name:"Node.js",imageSrc:node}
  ];
  const [settled1, setSettled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const element = document.querySelector('.animated-element1');
      const bounding = element.getBoundingClientRect();

      if (bounding.top < window.innerHeight - 100) {
        setSettled(true);
      } else {
        setSettled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);
  return (
    <sectiom id="skills" className={`animated-element1 ${settled1 ? 'settled1' : ''}`}>
        <span className="skillTitle">My Skills</span>
        <span className="skillDesc"></span>
        <div className="skillBars">
              {skills.map((e)=>{
                return <div className="skillBar" key={e.name}>
                <img src={e.imageSrc} alt="" className="skillBarImg" />
                <div className="skillBarText">
                    <h2>{e.name}</h2>
                </div>
                </div>
              })}   
        </div>
    </sectiom>
  )
}

export default Skills