import React, { useEffect, useState }  from 'react';
import './about.css';
import { Link } from 'react-scroll';
import rmbg from '../../assets/rmbg.png'
import { DownloadIcon } from '@chakra-ui/icons';
const About = () => {
  const [settled, setSettled] = useState(false);

    useEffect(() => {
      setSettled(true);
    }, []);
  const handleDownload1 = () => {
    window.open("https://drive.google.com/file/d/1DLd12jxxA0ehdZ05nuMkZxnOSGyBZSz_/view?usp=sharing", "_blank");
    const resumePath = '/Avishkar_Kamble.pdf';
    const link = document.createElement('a');
    link.href = resumePath;
    link.download = 'Avishkar-Kamble-Resume.pdf';
    link.click();
  };
  return (
    <div id="intro" className={` animated-element ${settled ? 'settled' : ''}`}>
        <div className="introcontent">
          {/* <Image src={rmbg} width={"20%"}/> */}
            <span className="hello">Hello,</span>
            <span className="introText">I'm <span className="introName">Avishkar</span></span>
            <p className="introPara"><span style={{backgroundColor:"white",color:"black"}}>A Fullstack Web Developer</span><br/><span style={{backgroundColor:"white",color:"black"}}>with a strong passion for creating stunning web experiences.</span></p>
            <Link to=""><button className="btn"  onClick={handleDownload1}><img src="" alt="" />Resume<DownloadIcon margin={"3px"}/></button></Link>
        </div>
        <img src={rmbg} alt="" className="bg" />
    </div>
  )
}

export default About