import React, { useState,useEffect } from 'react'
import './navbar.css'
import A from '../../assets/A.png'
import contact from '../../assets/contact.png'
import {Link} from 'react-scroll';
import { HamburgerIcon } from '@chakra-ui/icons'
const Navbar = () => {
    const [showMenu,setShowMenu]=useState(false)
    const [settled6, setSettled] = useState(false);
    useEffect(()=>{
      setSettled(true)
     },[])
    const handleDownload1 = () => {
      window.open("https://drive.google.com/file/d/1DLd12jxxA0ehdZ05nuMkZxnOSGyBZSz_/view?usp=sharing", "_blank");
      const resumePath = '/Avishkar_Kamble.pdf';
      const link = document.createElement('a');
      link.href = resumePath;
      link.download = 'Avishkar-Kamble-Resume.pdf';
      link.click();
    };
  return (
    <nav className={`navbar animated-element6 ${settled6 ? 'settled6' : ''}`}>
        <img src={A} alt="logo" className="logo" />
        <div className="desktopMenu"> 
        <Link activeClass='active' to='intro' spy={true} smooth={true} offset={-50} duration={500} className="desktopMenuListItem">About Me</Link>
        <Link activeClass='active' to='skills' spy={true} smooth={true} offset={-50} duration={500} className="desktopMenuListItem">Skills</Link>
        <Link activeClass='active' to='projects' spy={true} smooth={true} offset={-50} duration={500} className="desktopMenuListItem">Projects</Link>
        <Link activeClass='active' to='github' spy={true} smooth={true} offset={-50} duration={500} className="desktopMenuListItem">Github</Link>
        <Link activeClass='active' to="" onClick={handleDownload1} className="desktopMenuListItem">Resume</Link>
        </div>
        <button className="desktopMenuBtn" onClick={()=>{
            document.getElementById('contact').scrollIntoView({behavior:"smooth"})
        }}>
            <img src={contact} alt="" className="desktopMenuImg" />Contact Me
        </button>
        <div alt="menu" className="mobMenu" onClick={()=>{setShowMenu(!showMenu)}} ><HamburgerIcon/></div>
        <div className="navMenu" style={{display:showMenu? 'flex':'none'}}> 
        <Link activeClass='active' to='intro' spy={true} smooth={true} offset={-50} duration={500} className="ListItem" onClick={()=>{setShowMenu(false)}}>About Me</Link>
        <Link activeClass='active' to='skills' spy={true} smooth={true} offset={-50} duration={500} className="ListItem" onClick={()=>{setShowMenu(false)}}>Skills</Link>
        <Link activeClass='active' to='projects' spy={true} smooth={true} offset={-50} duration={500} className="ListItem" onClick={()=>{setShowMenu(false)}}>Projects</Link>
        <Link activeClass='active' to='github' spy={true} smooth={true} offset={-50} duration={500} >Github</Link>
        <Link activeClass='active' to='contact' spy={true} smooth={true} offset={-50} duration={500} className="ListItem" onClick={()=>{setShowMenu(false)}}>Contact</Link>
        <Link activeClass='active' to="" onClick={handleDownload1} className="ListItem">Resume</Link>
        </div>
    </nav>
  )
}

export default Navbar