import React, { useState,useEffect } from 'react';
import linkedin from '../../assets/linkedin.png';
import git from '../../assets/gitwhite.png';
import emailjs from '@emailjs/browser';
import { useRef } from 'react';
import './contact.css';
import ReactGitHubCalendar from 'react-github-calendar';
import { Image,Button,useDisclosure} from '@chakra-ui/react'
import {
  Modal,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalOverlay
} from '@chakra-ui/react';
import { useToast } from '@chakra-ui/react'
const Contact = () => {
    const [singleimage,setsingleimage]=useState("")
    const [name,setname]=useState("")
    const [email,setemail]=useState("")
    const [msg,setmsg]=useState("")
    const toast = useToast()
    const form=useRef();
    const [settled3, setSettled] = useState(false);
    const [settled4, setSettled4] = useState(false);
    const [settled5, setSettled5] = useState(false);
    useEffect(() => {
      const handleScroll = () => {
        const element = document.querySelector('.animated-element3');
        const element4 = document.querySelector('.animated-element4');
        const element5 = document.querySelector('.animated-element4');
        const bounding = element.getBoundingClientRect();
        const bounding4 = element4.getBoundingClientRect();
        const bounding5 = element5.getBoundingClientRect();
  
        if (bounding.top < window.innerHeight - 100) {
          setSettled(true);
        } else {
          setSettled(false);
        }
        if (bounding4.top < window.innerHeight - 100) {
          setSettled4(true);
        } else {
          setSettled4(false);
        }
        if (bounding5.top < window.innerHeight - 100) {
          setSettled5(true);
        } else {
          setSettled5(false);
        }
      };
      window.addEventListener('scroll', handleScroll);
      return () => {
        window.removeEventListener('scroll', handleScroll);
      };
    }, []);
    const sendEmail = (e) => {
        e.preventDefault();
        if(email.length!==0&&name.length!==0&&msg.email!==0){
            emailjs.sendForm('service_36n30qq', 'template_pcjy5u9', form.current, 'i31T4a4hzF4sSGNWV')
            .then((result) => {
              toast({
                  title: 'Success.',
                  description: "Message Send Successfully.",
                  status: 'success',
                  duration: 3000,
                  isClosable: true,
                })
                alert("Message Sent Successfully")
                e.target.reset()
            }, (error) => {
                console.log(error.text);
            });
        }
        else{
           alert("Please Enter All Details")
        }
        
       
      };
      const OverlayOne = () => (
        <ModalOverlay
        bg='none'
        backdropFilter='auto'
        backdropInvert='80%'
        backdropBlur='2px'
        closeOnOverlayClick={true}
        />
      )
      const { isOpen, onOpen, onClose } = useDisclosure()
      const [overlay, setOverlay] = React.useState(<OverlayOne />)
   
  return (
    <div className='contactPage'>
        <div id="github" className={`animated-element3 ${settled3 ? 'settled3' : ''}`}>
            <h1 className="contactPageTitle">Github</h1>
            <div className="clientImgs">
                <img src={"https://github-readme-streak-stats.herokuapp.com?user=aavishkark"} style={{cursor:"pointer"}} alt="git1" className="clientImg" onClick={()=>{setOverlay(<OverlayOne />);setsingleimage("https://github-readme-streak-stats.herokuapp.com?user=aavishkark"); onOpen()}}/>
                <img src={"https://github-readme-stats.vercel.app/api/top-langs/?username=aavishkark"} style={{cursor:"pointer"}} alt="git2" className="clientImg" onClick={()=>{setOverlay(<OverlayOne />);setsingleimage("https://github-readme-stats.vercel.app/api/top-langs/?username=aavishkark"); onOpen()}}/>
                <img src={"https://github-readme-stats.vercel.app/api?username=aavishkark"} style={{cursor:"pointer"}} alt="git3" className="clientImg" onClick={()=>{setOverlay(<OverlayOne />);setsingleimage("https://github-readme-stats.vercel.app/api?username=aavishkark"); onOpen()}}/>
            </div>
        </div>
         <div className={`github-calendar-container animated-element5 ${settled5 ? 'settled5' : ''}`} style={{cursor:"pointer"}} onClick={()=>{setOverlay(<OverlayOne />);setsingleimage("calender");onOpen()}}> 
      <ReactGitHubCalendar
        username="aavishkark"
        fullYear={false}
      />
      </div>
      <Modal isCentered isOpen={isOpen} onClose={onClose} >
        {overlay}
        <ModalContent paddingTop={"100px"}>
          <ModalHeader fontSize={"x-large"}></ModalHeader>
          <ModalBody display={"flex"} justifyContent={"center"}>
            {singleimage==='calender'?<div className="github-calendar-container" onClick={()=>{setsingleimage("calender")}}> 
      <ReactGitHubCalendar
        username="aavishkark"
        fullYear={false}
      />
      </div>:<Image src={singleimage} width={"50%"} overflow={"visible"} alignSelf={"center"}/>}
          </ModalBody>
          <ModalFooter display={'flex'} justifyContent={"start"} marginTop={"20px"}>
            <Button onClick={onClose} style={{cursor:"pointer"}} backgroundColor={"white"} border={"none"} padding={"5px 15px"} borderRadius={"10px"}>Close</Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
        <div id="contact" className={`animated-element4 ${settled4 ? 'settled4' : ''}`}>
           <h1 className="contactPageTitle">Contact Me</h1>
           <form className='contactFrom' ref={form} onSubmit={sendEmail}>
            <input type="text" className="name" name='name' value={name} onChange={((e)=>{setname(e.target.value)})} placeholder='Your Name' />
            <input type="email" className="email" name='email' value={email} onChange={((e)=>{setemail(e.target.email)})} placeholder='Your Email'/>
            <textarea className='msg' name="message" rows="5" value={msg} onChange={((e)=>{setmsg(e.target.value)})} placeholder='Your Message'></textarea>
            <button type="submit" value="Submit" className='submitBtn' color='black' style={{cursor:"pointer"}}>Submit</button>
            <div className="links">
                <a href='https://www.linkedin.com/in/avishkar-kamble-426830202/'><img src={linkedin} alt="linkedin" className="link" style={{cursor:"pointer"}} /> </a>
                <a href='https://github.com/aavishkark'><img src={git} alt="git" className="link" style={{cursor:"pointer"}}/></a>
                <span style={{alignSelf:"center",color:"rgb(0, 157, 255)",fontWeight:"bold",padding:"5px",borderRadius:"5px"}}>+918605611557</span>
            </div>
           </form>
        </div>
    </div>
  )
}

export default Contact